import { Component, OnInit } from '@angular/core';
import { UserService } from '../../../../../auth/_services/user.service';
import { QuickSidebarComponent } from '../../../../layouts/quick-sidebar/quick-sidebar.component';
// import { SideBarService } from '../../../../layouts/side-bar/side-bar.service';
import { SideBarToggleComponent } from '../../../../layouts/side-bar-toggle/side-bar-toggle.component';
import { DataService } from '../../../../../_services/data.service';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

@Component({
    selector: 'app-user-add',
    templateUrl: "user-add.component.html",
    styles: []
})
export class UserAddComponent implements OnInit {


    form: FormGroup;
    private formSumitAttempt: boolean;
    public vendorDetails: any = {};
    public userRole: any = {};
    public isReadOnly = true;
    public ImgUrl: any;

    constructor(private imgBaseUrl: ScriptLoaderService, private formBuilder: FormBuilder, private router: Router, private toastr: ToastrService, private route: ActivatedRoute, private _dataService: DataService, private _userService: UserService) { }

    ngOnInit() {


        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.subscription_list != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        this.form = this.formBuilder.group({
            username: [null, Validators.required],
            email: [null, Validators.required],
            contact: [null, Validators.required],
            country: [null, Validators.required],
            state: [null, Validators.required],
            city: [null, Validators.required],
            address: [null, Validators.required],
            zipcode: [null, Validators.required],
            facebook: [null, Validators.required],
            google_plus: [null, Validators.required],
            twitter: [null, Validators.required],
            linkedin: [null, Validators.required],
            youtube: [null, Validators.required],
            instagram: [null, Validators.required],
            flicker: [null, Validators.required],
            role: [null, Validators.required]
        });


        this._userService.getUserRoleAll().subscribe(
            data => {
                this.userRole = data.success;
            });

        this.ImgUrl = this.imgBaseUrl._imgBaseUrl;
        this._dataService.setloaderState(false);
        this._dataService.setactiveMenu('vendor');
        this.route.params.subscribe(
            params => {
                localStorage.setItem('section', 'vendor');
                this._userService.getById(params.id).subscribe(
                    data => {
                console.log(data);

                        this.vendorDetails = data.success;
                    }
                );
            }
        );


    }

    isFieldValid(field: string) {
        return (
            (!this.form.get(field).valid && this.form.get(field).touched) ||
            (this.form.get(field).untouched && this.formSumitAttempt)
        );
    }



    displayFieldCss(field: string) {
        return {
            'has-error': this.isFieldValid(field),
            'has-feedback': this.isFieldValid(field)
        };
    }



    onSubmit() {
        let input: any;
        this.formSumitAttempt = true;
        if (this.form.valid) {
            input = { form_data: this.form.value };
            this._userService.create(input).subscribe(
                data => {
                    this.toastr.success('User Added Successfully', 'Success Message');
                    this.reset();
                },
                (error: any) => {
                    console.log(error);
                    console.log('error');
                }
            );
        }
    }

    reset() {
        this.form.reset();
    }

}
